.contacts-form{border-top:1px solid #e0e0e0;padding:74px 0}.contacts-form__content{margin-top:30px;display:flex;justify-content:space-between;flex-wrap:wrap;color:#4f4f4f}.contacts-form__info{width:40%}.contacts-form__message{width:55%}.contacts-form__label{display:block;font-weight:400;font-size:24px;line-height:28px;position:relative;margin-top:20px}.contacts-form__input,.contacts-form__textarea{width:100%;margin-top:5px;display:block;padding:28px;border:1px solid #bdbdbd;outline:rgba(0,0,0,0);font-size:18px;margin-bottom:3px}.contacts-form__input::placeholder,.contacts-form__textarea::placeholder{font-family:"Roboto",sans-serif;font-weight:300;font-size:18px;line-height:21px;color:#bdbdbd}.contacts-form__input:focus,.contacts-form__textarea:focus{border:1px solid #dee203;box-shadow:1px 1px 10px rgba(250,255,9,.6)}.contacts-form__textarea{margin-bottom:5px;resize:none;height:235px}.contacts-form__btn{display:block;margin-top:15px;width:290px;height:70px;background:#212020;font-size:24px;line-height:28px;text-align:center;color:#f2f2f2;font-weight:300;outline:rgba(0,0,0,0);cursor:pointer;transition:all .5s ease-out}.contacts-form__btn:hover{background:#000}.contacts-form__agree{position:relative}.contacts-form__checkbox{opacity:0;position:absolute;z-index:1;width:15px;height:15px}.contacts-form__checkbox:checked+.contacts-form__checkbox__label::after{color:#fff}.contacts-form__checkbox__label{font-weight:300;font-size:14px;line-height:20px;color:#333;position:relative;padding-left:25px}.contacts-form__checkbox__label span{border-bottom:1px solid #333}.contacts-form__checkbox__label::after{content:"âœ“";position:absolute;color:#333;display:flex;justify-content:center;align-items:center;left:0px;top:-2px;width:17px;height:17px;background:#333;z-index:-1}.contacts-form .form__error{margin-bottom:0}@media(max-width: 1090px){.contacts-form__info,.contacts-form__message{width:100%}}@media(max-width: 690px){.contacts-form{padding:45px 0}.contacts-form__content{margin-top:40px}.contacts-form__label{font-size:18px;line-height:22px;margin-top:10px}.contacts-form__input,.contacts-form__textarea{padding:18px;font-size:16px}.contacts-form__input::placeholder,.contacts-form__textarea::placeholder{font-size:16px}.contacts-form__textarea{height:180px}.contacts-form__btn{margin:15px auto 0;width:200px;height:50px;font-size:20px}}